@import url(https://fonts.googleapis.com/css2?family=Mada&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mada&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mada&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mada&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mada&display=swap);
*{
  margin: 0;
  padding: 0;
}


.App{
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(90deg, #51E1ED, #23C4ED);
    width: 100%;
    height: 100vh;
}

.circle1{
    height: 200px;
    width: 200px;
    background-color: rgba(255, 166, 0, 0.468);
    position: absolute;
    top: 70%;
    left: 5%;
    border-radius: 50%;
    z-index: 1;
}

.circle2{
    height: 200px;
    width: 200px;
    background-color: rgba(255, 166, 0, 0.427);
    position: absolute;
    top: 10%;
    right: 5%;
    border-radius: 50%;
    z-index: 1;
}

.container{
    display: flex;
    height: 80%;
    width: 70%;
    background: #ffffff54;
    border-radius: 10px;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(20px);
    z-index: 3;
}

@media screen and (max-width: 800px){
    .container{
        display: flex;
        width: 90%;
        flex-direction: column;
    }
}
.side-nav{
    display: flex;
    height: 100%;
    flex-basis: 25%;
    flex-direction: column;
    border: 2px solid #ffffff;
    border-radius: 10px;
}

@media screen and (max-width: 800px){
    .side-nav{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }
}

.profile{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.dp{
    margin-top: 20px;
    width: 150px;
    height: 150px;
    background: url(/avinash-portfolio/static/media/avinash_img.d4ee406f.jpeg);
    background-size: cover;
    border-radius: 50%;
    align-self: center;
}

.name-text{
    margin-top: 10px;
    align-self: center;
    font-family: 'Mada', sans-serif;
    font-size: 20px;
    text-decoration: underline;
}

.options{
    height: 40%;
    margin-top: 50px;
    margin-bottom: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

}

@media screen and (max-width: 800px){
    .options{
        height: 80%;
        margin-top: 25px;
    }
}

.options h3{
    font-size: 18px;
    font-family: 'Mada', sans-serif;
    color: #1f1f1f;
    padding: 5px 10px;
    background-color: #ffffff;
    border-radius: 10px;
    transition: color 300ms ease-in, background-color 300ms ease-in;
}

.options h3:hover{
    color: #ffffff;
    background-color: #1f1f1f;
    cursor: pointer;
}

.About-container{
    display: flex;
    flex-basis: 75%;
    flex-direction: column;
    align-items: center;
}

.about-heading{
    margin-top: 25px;
    font-family: 'Mada', sans-serif;
    text-decoration: underline;
}

.about-para{
    margin-top: 35px;
    width: 80%;
    text-align: center;
    font-family: 'Mada', sans-serif;
}

.resume{
    margin-top: 50px;
    padding: 20px 40px;
    border-radius: 10px;
    border: 2px solid #1f1f1f;
    font-size: 20px;
    background-color: #ffffff;
    color: #1f1f1f;
    font-weight: bold;
    font-family: 'Mada', sans-serif;
    text-decoration: none;
}

.resume:hover{
    background: transparent;
}

@media screen and (min-width: 801px){
    .random-image{
        margin-top: 60px;
        width: 80%;
        height: 150px;
        background: url(/avinash-portfolio/static/media/businessmen-42691_1280.e54c2fa0.png);
        background-size: cover;
    }
}
.skills-container{
    display: flex;
    flex-direction: column;
    flex-basis: 75%;
    align-items: center;
}

.skills-title{
    margin-top: 25px;
    font-family: 'Mada', sans-serif;
    text-decoration: underline;
}

.icon-containers{
    margin-top: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 100%;
}

.card{
    display: flex;
    flex-basis: 25%;
    justify-content: center;
    align-items: center;
    height: 100px;
    border: 2px solid black;
    box-shadow: 1px 1px 2px 1px #1f1f1f7b;
    background-color: rgba(255, 235, 205, 0.968);
    border-radius: 10px;
}

.card:hover{
    box-shadow: none;
    background-color: transparent;
}

.tooltip-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.tooltip-text{
    font-family: 'Mada', sans-serif;

}
.project-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-basis: 75%;
}

.project-heading{
    margin-top: 25px;
    font-family: 'Mada', sans-serif;
    text-decoration: underline;
}

.project-card-container{
    display: flex;
    flex-wrap: wrap;
    margin-top: 40px;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.project-card{
    display: flex;
    flex-basis: 40%;
    height: 150px;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
    border-radius: 10px;
    box-shadow: 1px 1px 2px 1px #1f1f1f87;
    text-decoration: none;
}

@media  screen and (max-width: 800px) {
    .project-card{
        margin-bottom: 25px;
    }
}

.project-card:hover{
    box-shadow: none;
}

.back1{
    background: linear-gradient(90deg, #1f1f1f64, #1f1f1fa3),
    url(/avinash-portfolio/static/media/proj1.777b3c95.jpg);
    background-size: cover;
}

.back2{
    background: linear-gradient(90deg, #1f1f1f64, #1f1f1fa3),
    url(/avinash-portfolio/static/media/corona.10cdd04b.jpg);
    background-size: cover;
}

.back3{
    background: linear-gradient(90deg, #1f1f1f64, #1f1f1fa3),
    url(/avinash-portfolio/static/media/portfolio.421c8d23.jpg);
    background-size: cover;
}

.project-text{
    color: #ffffff;
    font-family: 'Mada', sans-serif;
    letter-spacing: 1.5px;
}
.contact-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-basis: 75%;
}

.contact-heading{
    margin: 25px;
    font-family: 'Mada', sans-serif;
    text-decoration: underline;
}

.contact-text{
    margin-top: 30px;
    width: 80%;
    font-family: 'Mada', sans-serif;
    color: #1f1f1f;
    font-size: 16px;
}

.social-media-container{
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 80%;

}

.social-icon-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 25%;
    height: 100px;
    background-color: rgb(255, 235, 205);
    border-radius: 10px;
    box-shadow: 1px 1px 2px 1px #1f1f1f82;

}

@media screen and (max-width: 800px){
    .social-icon-container{
        height: 70px;
    }
}

.social-icon-container:hover{
    background-color: transparent;
}

.social-icons{
    width: 80%;
    height: 70%;
}

.done-img{
    margin-top: 70px;
    background: url(/avinash-portfolio/static/media/approved-151676_1280.dced9902.png);
    width: 80px;
    height: 80px;
    background-size: cover;
}
